* {
  padding: 0;
  margin: 0;
}



body {
  font-family: 'Karla', sans-serif;
  height: 100vh;
  /* background: linear-gradient(to bottom right, #233D4D, #619B8A); */
  background-color: #133950;
  text-align: center;
  color: #CDCDCD;
}

.quote {
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Spectral', serif;
  color: #fff;
}

h1 {
  padding: 24px;
}

.button-generate {
  background-color: #e7e7e7;
  border: none;
  padding: 10px;
  border-radius: 2px;
}

.button-generate:hover {
  cursor: pointer;
}

.content {
  max-width: 75%;
  margin: auto;
  transition: 0.25s;
}

.showing {
  opacity: 1;
}

.hidden {
  opacity: 0;
}

.orange-divider {
  background: #D8973C;
  margin: 24px 48px;
  border: 0;
  height: 1px;
}

.character {
  color: #fff;
  padding: 24px;
  float: right;
  font-weight: bold;
  font-style: italic;
  font-size: 18px;
}


@media(min-width: 768px) {
  .container {
    width: 60%;
    margin: auto;
  }
}